import CommonHeader from '@/components/layouts/CommonHeader.vue';
import graceFullLoading from "@/graceUI/components/graceFullLoading.vue"

export default {
	components: {
		CommonHeader,
		graceFullLoading
	},
	data() {
		return {
			info: {},
			loading: true,
			specClass: '', //规格弹窗css类，控制开关动画
			selectSpec: null, //选中规格
			title: '',
			number: 1,
			shareClass: 'hide'
		};
	},
	onLoad(e) {
		this.getInfo(e.id)
	},
	onReady() {
		//计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
	},
	methods: {
		goCart() {
			uni.switchTab({
				url: '/pages/cart/cart'
			})
		},
		getInfo(id) {
			uni.request({
				url: getApp().globalData.api + 'product/info',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: id
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.info = res.data.data
					this.title = res.data.data.name
					var richtext = this.info.content
					const regex = new RegExp('<img', 'gi')
					this.info.content = richtext.replace(regex, '<img style="max-width: 100%;"')
					this.loading = false
				},
				fail: (res) => {
					this.loading = false
				}
			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		goStore(id) {
			uni.navigateTo({
				url: "/pages/store/online_store?id=" + id
			})
		},
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '/pages/chat/chat_info'
			})
		},
		// 客服
		toChat() {
			var chater_info = {
				username: 'kefu_' + this.info.id,
				nickName: this.info.store_name
			}
			uni.setStorage({
				key: 'chater_info',
				data: chater_info
			})
			uni.navigateTo({
				url: '/pages/chat/chat_info',
				animationDuration: 300
			})
		},
		// 分享
		share() {
			this.shareClass = 'show';
		},
		hideShare() {
			this.shareClass = 'hide';
			setTimeout(() => {
				this.shareClass = 'none';
			}, 150);
		},
		//收藏
		collect() {
			uni.request({
				url: getApp().globalData.api + 'product/collect',
				method: 'POST',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: this.info.id,
					status: this.info.is_collect == 1 ? 0 : 1
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.info.is_collect = this.info.is_collect == 1 ? 0 : 1;
				}
			});
		},
		// 加入购物车
		joinCart() {
			if (this.selectSpec == null) {
				return this.showSpec(() => {
					// 加入购物车
					uni.request({
						url: getApp().globalData.api + 'product/add-cart',
						method: 'POST',
						data: {
							open_id: uni.getStorageSync('open_id'),
							product_id: this.info.id,
							store_id: this.info.store_id,
							amount: this.number,
							product_sku_id: this.selectSpec,
						},
						header: {
							'content-type': 'application/json' //自定义请求头信息
						},
						success: (res) => {
							this.selectSpec = null
							if (res.data.code == 200) {
								uni.showToast({
									title: "已加入购物车"
								});
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								});
							}
						}
					});
				});
			}
			uni.showToast({
				title: "已加入购物车"
			});
		},
		//立即购买
		buy() {
			if (this.selectSpec == null) {
				return this.showSpec(() => {
					this.toConfirmation();
				});
			}
			this.toConfirmation();
		},
		//跳转确认订单页面
		toConfirmation() {
			let tmpList = [];
			let goods = {
				id: this.goodsData.id,
				img: '/static/logo.png',
				name: this.goodsData.name,
				spec: '规格:' + this.goodsData.spec[this.selectSpec],
				price: this.goodsData.price,
				number: this.goodsData.number
			};
			tmpList.push(goods);
			uni.setStorage({
				key: 'buylist',
				data: tmpList,
				success: () => {
					uni.navigateTo({
						url: '../order/confirmation'
					})
				}
			})
		},
		//跳转评论列表
		toEvalues(id) {
			uni.navigateTo({
				url: '/pages/product/ratings?id=' + id
			})
		},
		//选择规格
		setSelectSpec(id) {
			console.log(id)
			this.selectSpec = id;
		},
		//减少数量
		sub() {
			if (this.number <= 1) {
				return;
			}
			this.number--;
		},
		//增加数量
		add() {
			this.number++;
		},
		//跳转锚点
		toAnchor(index) {
			this.selectAnchor = index;
			uni.pageScrollTo({
				scrollTop: this.anchorlist[index].top,
				duration: 200
			});
		},
		//计算锚点高度
		calcAnchor() {
			this.anchorlist = [{
					name: '主图',
					top: 0
				},
				{
					name: '评价',
					top: 0
				},
				{
					name: '详情',
					top: 0
				}
			]
			let commentsView = uni.createSelectorQuery().select("#comments");
			commentsView.boundingClientRect((data) => {
				let statusbarHeight = 0;
				//APP内还要计算状态栏高度



				let headerHeight = uni.upx2px(100);
				this.anchorlist[1].top = data.top - headerHeight - statusbarHeight;
				this.anchorlist[2].top = data.bottom - headerHeight - statusbarHeight;

			}).exec();
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback() {
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec && this.specCallback && this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		discard() {
			//丢弃
		}
	}
};
